@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&family=Roboto+Condensed:wght@300;400;700&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: "Poppins", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
p {
  font-size: 1.6rem;
}
h1 {
  font-size: 5rem;
}
.App {
  width: 100%;
  height: 100vh;
  position: relative;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.4);
}
.App:before {
  content: "";
  background: url(./assests/sunset.jpg) no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}
.App .search {
  text-align: center;
  padding: 1rem;
}
.App input {
  padding: 0.6rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  background: rgba(255, 255, 255, 0.1);
  color: #f8f8f8;
}
::placeholder {
  color: #fff;
}

.container {
  max-width: 700px;
  height: 450px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.App .top {
  width: 100%;
  margin: 1rem auto;
}
.App .description {
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(269deg);
}
.App .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1re auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.2);
}
.search {
  display: flex;
  justify-content: center;
}
.search-icon .icon {
  font-size: 30px;
  margin-left: 1rem;
  margin-top: 1rem;
}
.search-icon .icon:hover {
  transition: 0.3s;
  transform: scale(1.2);
}
.bold {
  font-weight: 700;
}
